

















import {Vue, Component, Prop, Mixins} from 'vue-property-decorator';
import HTitle from '@/components/htable/HTitle.vue';
import echarts, {EChartOption} from 'echarts';
import ResizeMixin from '@/mixins/resize';

@Component({
  components: {
    HTitle,
  },
})
export default class PointTotal extends Mixins(ResizeMixin) {
  @Prop({default: 'chart'}) private className!: string;
  @Prop({default: '100%'}) private width!: string;
  @Prop({default: '340px'}) private height!: string;

  public mounted() {
    this.$nextTick(() => {
      this.initChart();
    });
  }

  private initChart() {
    const keys: string[] = ['10-1', '10-2', '10-3', '10-4', '10-5', '10-6'];
    const totalPoints: number[] = [10, 8, 6, 5, 4, 3];
    const payPoints: number[] = [2, 1, 5, 2, 4, 2];
    this.chart = echarts.init((this.$refs as any).chart as HTMLDivElement);
    this.chart.setOption({
      tooltip: {
        trigger: 'axis',
        axisPointer: {            // 坐标轴指示器，坐标轴触发有效
          type: 'shadow',        // 默认为直线，可选为：'line' | 'shadow'
        },
      },
      legend: {
        orient: 'vertical',
        left: 'right',
        top: 20,
        data: ['总积分', '可兑换积分'],
        itemWidth: 10,
        itemHeight: 10,
      },
      grid: {
        top: '30%',
        left: '3%',
        right: '4%',
        bottom: '4%',
        containLabel: true,
      },
      xAxis: [
        {
          type: 'category',
          data: keys,
        },
      ],
      yAxis: [
        {
          type: 'value',
        },
      ],
      series: [
        {
          name: '可兑换积分',
          type: 'bar',
          stack: '可用积分',
          barWidth: '20%',
          data: payPoints,
        },
        {
          name: '总积分',
          type: 'bar',
          stack: '总积分',
          barWidth: '20%',
          data: totalPoints,
        },
      ],
      color: ['#5AD8A6', '#5B8FF9'],
    } as any);
  }
}
