













import {Vue, Component, Prop, Mixins} from 'vue-property-decorator';
import HTitle from '@/components/htable/HTitle.vue';
import echarts, {EChartOption} from 'echarts';
import ResizeMixin from '@/mixins/resize';

@Component({
  components: {
    HTitle,
  },
})
export default class RedpackTotal extends Mixins(ResizeMixin) {
  @Prop({default: 'chart'}) private className!: string;
  @Prop({default: '100%'}) private width!: string;
  @Prop({default: '340px'}) private height!: string;

  public mounted() {
    this.$nextTick(() => {
      this.initChart();
    });
  }

  private initChart() {
    const keys: string[] = ['星期一', '星期二', '星期三', '星期四', '星期五', '星期六', '星期日'];
    const values: number[] = [1, 6, 5, 7, 9, 6, 10];
    this.chart = echarts.init((this.$refs as any).chart as HTMLDivElement);
    this.chart.setOption({
      grid: {
        top: '30%',
        left: '3%',
        right: '4%',
        bottom: '4%',
        containLabel: true,
      },
      xAxis: {
        type: 'category',
        boundaryGap: false,
        data: keys,
      },
      yAxis: {
        type: 'value',
      },
      series: [{
        data: values,
        type: 'line',
        label: {
          normal: {
            show: true,
            position: 'top',
          },
        },
        areaStyle: {
          normal: {
            color: '#FDD6D6',  // 改变区域颜色
          },
        },
        itemStyle: {
          normal: {
            color: '#F95B5B', // 改变折线点的颜色
            lineStyle: {
              color: '#F95B5B', // 改变折线颜色
            },
          },
        },
      }],
    } as any);
  }
}
